
@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@300;400;500;600&display=swap');
*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}
:root{
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77,181,255,0.4);
    --color-white: #fff;
    --color-light: rgba(255,255,255,0.6);
    --first-color: hsl(353,100%,65%);
    --title-color: hsl(244,24%,26%);
    --text-color: hsl(244,16%,43%);
    --body-color: hsl(258,60%,98%);

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-ms: 98%;
    --transition : all 400ms ease;

    --shadow: 0px 5px 20px 0px rgb(69 67 96 / 10%);


    

}
html{
    scroll-behavior: smooth;
}
::-webkit-scrollbar{
    display: none;
}
body{
    font-family: 'Oswald', sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(../src/assets/stars.png),url(../src/assets/mountains_front.png),url(../src/assets/mountains_behind.png);
    background-repeat:space;
}


/* =====BREAKING THE CODE INTO PARTS ====== */

.container{
    width: var(--container-width-lg); /* width for the screens here */
    margin: 0 auto; /* To position everything in the middle horizontally */
}
h1,
h2,
h3,
h4,
h5{
    font-weight: 500;
}
h1{
    font-size: 2.5rem;
}
section{
    margin-top: 8rem;
}
section > h2, section >h5 {
    text-align: center;
    color: var(--color-light);
}
section > h2{
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light{
    color: var(--color-light)
}
a {
    color: var(--color-primary);
    transition: var(--transition);
}
a:hover{
    color: var(--color-white);
}
.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}
.btn:hover{
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}
.btn-primary{
    background: var(--color-primary);
    color: var(--color-bg);
}
img{
    display: block;
    width: 100%;
    object-fit: cover;
}

/* THE MEDIA QUERIES FOR MEDIUM DEVICES */
@media screen and (max-width: 1024px) {
    .container{
        width: var(--container-width-md);
    }
    section{
        margin-top: 6rem;
    }
}
/* THE MEDIA QUERIES FOR SMALL DEVICES */
@media screen and (max-width: 600px) {
    .container{
        width: var(--container-width-ms);
    }
    section > h2{
        margin-bottom: 2rem;
    }
}